.form1 h2 {
  margin: 1rem 0;
  color: black;
  font-size: 25px;
  padding-bottom: 0px;
}

.form1 {
  display: block;
  max-width: 800px;
  margin: 0 auto;
}

.inputs {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  display: block;
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  min-width: 20%;
  min-height: 2.5rem;
  border-radius: 4px;
  border: 1px solid black;
  padding: 0.7rem 1rem;
  margin-bottom: 1rem;
  font-size: 14px;
}

.form1 .label,
section > label {
  line-height: 2;
  text-align: left;
  display: block;
  margin-bottom: 13px;
  margin-top: 20px;
  color: white;
  font-size: 14px;
  font-weight: 200;
}

.submit {
  position: relative;
  background: #ec5990;
  color: white;
  text-transform: uppercase;
  border: none;
  font-weight: 600;
  margin-top: 20px;
  padding: 20px;
  font-size: 16px;
  letter-spacing: 2px;
  display: block;
  appearance: none;
  border-radius: 4px;
  width: 100%;
  font-weight: 400;
  letter-spacing: 0.5rem;
  transition: 0.3s all;
}

.form1 button[type='button'] {
  padding: 5px;
  background: #516391;
  color: white;
  letter-spacing: 0px;
  text-transform: none;
  padding: 10px;
  letter-spacing: 1px;
}

.form1 input[type='submit']:hover,
button[type='button']:hover {
  background: #bf1650;
  color: white;
}

.form1 input[type='submit']:active {
  transition: 0.3s all;
  top: 2px;
}

.invalidInput {
  margin: -0.5rem 0 0.5rem 0;
  color: #ef1650;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}